nav#top-nav {
    @apply capitalize max-w-full mx-auto px-6 sticky h-full top-0 z-50 pt-8 md:pt-2 md:mb-2 p-4;
}

nav#top-nav .logo {
    @apply z-30 text-base w-full md:w-fit flex justify-between items-center;
}

nav#top-nav .logo > * {
    @apply basis-1/3 md:basis-full;
}

nav#top-nav .logo h2 {
    @apply basis-1/2 md:basis-full text-base sm:font-semibold sm:text-lg;
}

nav#top-nav .logo .icons {
    @apply flex justify-between items-center gap-2 md:hidden;
}

nav#top-nav .logo .toggle-menu {
    @apply cursor-pointer md:hidden;
}

nav#top-nav .logo .toggle-menu.close .line {
    @apply border border-white h-0.5 rounded-lg w-6 sm:w-8 transition-all duration-1000;
}

nav#top-nav .logo .toggle-menu.open .line {
    @apply border border-white h-0.5 rounded-lg w-6 sm:w-8 mt-2 transition-all duration-1000;
}

nav#top-nav .logo .toggle-menu.open .line:first-child {
    @apply rotate-45;
}

nav#top-nav .logo .toggle-menu.open .line:last-child {
    @apply -translate-y-2 -rotate-45;
}

nav#top-nav .navlinks-container {
    @apply md:max-h-full pr-8 md:pr-0 md:pt-0 md:basis-3/4 flex flex-col items-center md:flex-row justify-center md:justify-between w-screen fixed md:static top-0 left-0 right-0 bottom-0 transition-all md:transition-none duration-300 overflow-hidden;
}

nav#top-nav ul.navlinks {
    @apply w-full md:w-fit text-right md:text-center md:mb-2;
}

nav#top-nav ul.navlinks li {
    @apply block mb-12 md:mb-0 text-3xl mt-6 md:mt-0 md:text-normal md:inline-block md:ml-4 md:relative;
}

nav#top-nav ul.navlinks .active li::after {
    @apply h-1 w-1 absolute left-1/2 -bottom-1 bg-black;
}

nav#top-nav ul.navlinks .active li {
    @apply line-through decoration-black md:no-underline text-lg md:text-base;
}

nav#top-nav ul.navlinks li::after {
    content: ""; 
    @apply h-0.5 w-0 rounded-xl absolute left-0 -bottom-2 bg-primary transition-all ease-in-out duration-300;
} 

nav#top-nav ul.navlinks li:hover::after {
    @apply md:w-full;
}

nav#top-nav ul.navlinks .active li:hover::after {
    @apply md:w-1;
}

nav#top-nav ul.navlinks li.auth {
    @apply md:hidden;
}

nav#top-nav .options {
    @apply flex justify-between items-center;
}

nav#top-nav .options > * {
    @apply md:inline-block mr-2 md:mr-6;
}

nav#top-nav .options svg {
    @apply hidden md:block
}

nav#top-nav .options .auth {
    @apply hidden md:block
}

nav#top-nav .options .auth button {
    @apply bg-yellow-500 font-bold rounded-2xl px-4 py-2;
}